@import '../app/appBasicStyleSetting';

.app-orderItem {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  justify-content: space-around;

  form, div, p {
    margin: 0; } }

.app-orderItem--title {
  flex: 3; }

.app-orderItem--unitPrice {
  flex: 2; }

.app-orderItem--subTotal {
  flex: 5; }

.app-orderItem--qty {
  flex: 3; }

@media screen and (min-width: 768px) {
  .app-orderItem {
    flex-direction: row;
    align-items: center; } }
